import logo from './logo.svg';
import './App.css';
import { useDispatch,useSelector } from 'react-redux';
import { getDataFromApiAction, setColAction, setDataSecondDataHoursAction, setRowAction, setSecondDataAnalyzerHoursAction } from './store/DataReducer'
import { ParamsBlock } from './components/ParamsBlock';
import { changeParamsValueAction } from './store/ParamsReducer';
import { ToolBar } from './components/ToolBar';
import { ObjectsGrid,HoursGrid, HoursAnalyzGrid } from './components/DataGrid';
import { DisplaySettingsOutlined } from '@mui/icons-material';
import { useEffect } from 'react';
import 'react-data-grid/lib/styles.css'

function App() {
  const dispatch = useDispatch();
  let dtn = new Date(useSelector(state => state.paramsReducer.datn))
  const dataObj = useSelector(state => state.dataReducer.DataObjects);
  const dataHours = useSelector(state => state.dataReducer.DataHours);
  const secondDataHours = useSelector(state => state.dataReducer.secondDataHours);
  const dataHoursAnalyz = useSelector(state => state.dataReducer.DataHoursAnalyzer);
  const secondHoursAnalyz = useSelector(state => state.dataReducer.secondHoursAnalyzer);
  let mainGridRowId = useSelector(state => state.dataReducer.row_ind)
  let mainGridCol = useSelector(state => state.dataReducer.col)


  useEffect(() => {
    const obj1 = dataObj[mainGridRowId]
   
    
    const isMatch = dataHours.filter(obj2 =>
      obj2.id_obj === obj1.id
    );
    dispatch(setDataSecondDataHoursAction(isMatch))
    
   let isMatch2 = dataHoursAnalyz.filter(obj2 =>
      obj2.id_obj === obj1.id
    );
    
    dispatch(setSecondDataAnalyzerHoursAction(isMatch2))
  }, [mainGridRowId])

  let paramsObj ={
    datn: dtn
  }

  const fetchMainData = () =>{
    dispatch(getDataFromApiAction(paramsObj))
  }

  const inputOnChange = (key,val) => {
    dispatch(changeParamsValueAction(key,val.format()));
  }
  
  const MainGridRowClick = (rowId,colId) =>{
    dispatch(setRowAction(rowId))
    dispatch(setColAction(colId,''))
  }

  return (
    <div>
        <ToolBar fetchMainData = {fetchMainData}/>
        <ParamsBlock paramsObj = {paramsObj} inputOnChange = {inputOnChange} />
        <div className='conteiner'>
          <ObjectsGrid
           dataObj = {dataObj}
           onRowClick = {MainGridRowClick}
           currRowInd = {mainGridRowId}
           currColumn = {mainGridCol}
          />
          <HoursGrid
           dataObj = {secondDataHours}
          />
          
        </div>
        <div>
        <HoursAnalyzGrid
          dataObj = {secondHoursAnalyz}
        />
        </div>
    </div>
  );
}

export default App;
