import {DatePicker} from '@mui/x-date-pickers/DatePicker';
import {LocalizationProvider} from '@mui/x-date-pickers/LocalizationProvider';
import {AdapterMoment} from '@mui/x-date-pickers/AdapterMoment';
import 'moment/locale/uk';
import moment from 'moment';
import './style.css';

export const ParamsBlock = (props) => {
    return (
        <div>
            <LocalizationProvider dateAdapter={AdapterMoment}>
                <div className='block'>
                    <DatePicker id="datn" value = {moment(props.paramsObj.datn)}
                    sx = {{
                        width: "150px",
                        '& input': {padding: 0,paddingLeft:2,height:25,fontSize:15,fontWeight:700}
                    }}
                    onChange = {(newVal) => {props.inputOnChange('datn',moment(newVal)) }}
                    />     
                </div>    
            </LocalizationProvider>
        </div>
    )
}
