
import moment from 'moment';

let defaultState = {
    datn: Date()
}

const CHANGE_PARAMS_VALUE ="CHANGE_PARAMS_VALUE"

export const ParamsReducer = (state = defaultState, action) => {
    switch(action.type){
        case CHANGE_PARAMS_VALUE :{
            return {
                ...state, [action.payload.key]: action.payload.value
            }
        }
        default: return state
    }
}

export const changeParamsValue = (payload) => ({type: CHANGE_PARAMS_VALUE, payload})

export const changeParamsValueAction = (inputName,val) =>{
    return function (dispatch){
        let changeInput = {
            key: inputName,
            value: val
        }
        dispatch(changeParamsValue(changeInput))
    }
}