import axios from "axios";
import moment from "moment";

let defaultState = {
    DataObjects:[],
    DataHours:[],
    secondDataHours: [],
    DataHoursAnalyzer:[],
    secondHoursAnalyzer: [],
    DataCalculateHours:[],
    row_ind: null,
}

const GET_DATA_FROM_API = "GET_DATA_FROM_API"
const SET_ROW_ID = "SET_ROW_ID"
const SET_ROW = "SET_ROW"
const SET_COL = "SET_COL"
const SET_SECOND_DATA_HOURS = "SET_SECOND_DATA_HOURS"
const SET_SECOND_ANALYZER_DATA_HOURS = "SET_SECOND_ANALYZER_DATA_HOURS"

export const DataReducer = (state = defaultState, action) => {
    switch(action.type){
        case GET_DATA_FROM_API :{
           
            
            return {
                ...state,
                DataObjects: action.payload.objectsList,
                DataHours: action.payload.hoursList,
                DataHoursAnalyzer: action.payload.hoursAnalyzer
            }
        }
        case SET_SECOND_DATA_HOURS:{
            return {
                ...state,
                secondDataHours : action.payload
            }
        }
        case SET_SECOND_ANALYZER_DATA_HOURS:{
            return {
                ...state,
                secondHoursAnalyzer : action.payload
            }
        }
        case SET_ROW_ID:{
            if(action.payload !== null){
                return{
                    ...state, row_ind: action.payload
                }
            } else{
                return{
                    ...state, row_ind: null
                }
            }
        }
        case SET_ROW:{
            if(action.payload !== null){
              
                
                let rowInd = state.DataObjects.findIndex(r => r.id === action.payload)
                return{
                    ...state,row_ind: rowInd
                }
            } else {
                return{
                    ...state, row_ind: null
                }
            }
        }
        case SET_COL: {
            return {...state, col: action.payload.idx}
        }
        default: return state
    }
}

export const getDataFromApi = (payload) => ({type:GET_DATA_FROM_API, payload})
export const setRow = (payload) => ({type:SET_ROW, payload})
export const setRowId = (payload) => ({type:SET_ROW_ID, payload})
export const setCol = (payload) => ({type:SET_COL, payload})
export const setDataSecondDataHours = (payload) => ({type:SET_SECOND_DATA_HOURS, payload})
export const setSecondDataAnalyzerHours = (payload) => ({type:SET_SECOND_ANALYZER_DATA_HOURS, payload})

export const getDataFromApiAction = (params) => {
    return async function (dispatch) {
        try {
            let dtnStr = moment(params.datn).format("D-M-YYYY")
            let apiUrl = `https://localhost:7297/api/MainData?date=${dtnStr}`
            let fetchResult = await axios.get(apiUrl)
            dispatch(getDataFromApi(fetchResult.data))
            dispatch(setDataSecondDataHoursAction([]))
        } catch (error) {
            
        }
    }
}

export const setDataSecondDataHoursAction = (props) =>{
    return function (dispatch){
        dispatch(setDataSecondDataHours(props))
    }
}
export const setSecondDataAnalyzerHoursAction = (props) =>{
    return function (dispatch){
        dispatch(setSecondDataAnalyzerHours(props))
    }
}

export const setRowAction = (rowId) =>{
    return function (dispatch){
        dispatch(setRow(rowId))
    }
}
export const setRowIDAction = (rowId) =>{
    return function (dispatch){
        dispatch(setRowId(rowId))
    }
}
export const setColAction = (colId,colName) => {
    return function (dispatch) {
        let col = {
            idx: colId,
            name : colName
        }
        dispatch(setCol(col))
    }
}