import { useState, useEffect, useRef } from "react";
import DataGrid, { Row } from "react-data-grid";
import { useDispatch, useSelector } from "react-redux";
import {HoursAnalyzGridColums, HoursGridColums, ObjectsGridColums} from './GridColums'
import './style.css'
import 'react-data-grid/lib/styles.css'

export const ObjectsGrid = (props) =>{

    const [currow,Setcurrow] = useState(0)
    const [currCol, setCurrCol] = useState(0)

    useEffect(() =>{
        Setcurrow(props.currRowInd)
        setCurrCol(props.currColumn)
    },[props.currRowInd, props.currColumn])

    function rowKeyGetter(row){
        return row.id
    }
    const onRowClick = (cell) => {
        if(typeof cell.column !== "undefined"){
            props.onRowClick(cell.row.id, cell.column.idx, cell.column.key)
        }
    }
    // const rowsChange= (rows,change) =>{
    //     let updatedInd = change.indexes[0]
    //     let updatedRow = rows[updatedInd]
    //     let key = change.column.key
    //     props.mainGridRowUpdate(updateRow,change)
    // }
        
    const gridColumns = ObjectsGridColums (props) 
   
    return(
        <div>
            <DataGrid className= 'ObjGrid' 
            defaultColumnOptions= {{
                sortable : true,
                resizable : true,
                minWidth :0,
                maxWidth : 100,
                editable: false
            }}

            columns = {gridColumns}
            rows = {props.dataObj}
            rowHeight={30}
            headerRowHeight={60}
            rowKeyGetter={rowKeyGetter}
            onCellClick= {onRowClick}
            enableCellSelect = {true}
            />
        </div>
    )


}

